.TeamPage {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}
    .team-name-div{
        grid-column: span 3;
        justify-self: start;
        align-self: end;
      }

    .team-name{
        font-size: 4rem;
        margin-bottom: 20px;
    }

    .team-wins-losses-section{
        justify-self: center;
        padding: 30px;
        text-align: center;
    }

    .game-details-card{
        grid-column: span 4;
    }


    .more-link{
        align-self: center;
        justify-self: center;
        font-size: 1.5rem;
    }
    



