@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@1,200&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}


a {
  text-decoration: none;
}


body {
  font-family: 'Titillium Web', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  color: lightgray;
}


.App{padding:20px;text-align:left}.won-card{background-color:#4da375}.lost-card{background-color:#a34d5d}

.GameDetailsCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.GameDetailsCard .game-date,.GameDetailsCard .game-stadium,.GameDetailsCard .game-result{margin-top:10px}.GameDetailsCard .additional-details{text-align:right}.GameDetailsCard .additional-details>p{margin-bottom:20px}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-top:10px;margin-bottom:20px}

.GamePage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px}.GamePage .year-selector{justify-self:end;padding:20px}

.GameSmallCard{padding:10px;margin-bottom:20px}.GameSmallCard .game-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.team-name-div{grid-column:span 3;justify-self:start;align-self:end}.team-name{font-size:4rem;margin-bottom:20px}.team-wins-losses-section{justify-self:center;padding:30px;text-align:center}.game-details-card{grid-column:span 4}.more-link{align-self:center;justify-self:center;font-size:1.5rem}

.TeamsTile{height:150px;text-align:center;border:1px solid white;padding-top:50px}

.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.HomePage .header-name{font-size:3rem;text-align:center;margin:30px}

